.button-wrapper {
  justify-content: flex-end;
  align-items: center;
  margin: 0 30px;
  padding: 30px 0;
  display: flex;
}

.btn {
  text-align: center;
  text-transform: uppercase;
  color: rgba(96, 94, 94, .62);
  background-image: linear-gradient(to right, #a1c4fd 0%, #c2e9fb 51%, #a1c4fd 100%);
  background-size: 200%;
  border: none;
  border-radius: 10px;
  justify-content: flex-end;
  align-items: center;
  padding: 25px;
  transition: all .9s;
  display: flex;
  box-shadow: 0 0 20px #eee;
}

.btn:hover, .btn:focus {
  background-image: linear-gradient(to left, #84fab0 0%, #8fd3f4 51%, #84fab0 100%);
  background-position: 100%;
}

.hidden {
  display: none;
}

